}

.weatherImage{
    height: 50px;
    width:50px;
    background-image: url("./scattered_clouds.png");
    border:none;
}

.home-background{
  background:url("./mancitywall.jpg")no-repeat;
  background-size: cover;
  height:600px; 

}




