.App {
  text-align: center;
  position: relative;
  top: 120px;

}

.weather{
  height: 300px;
  width: 300px;
  display: -ms-flexbox;
  display: flex;
  position: relative;;



}

.home-background{
  background:url("./mancitywall.jpg");
  background-repeat:no-repeat;
  background-size:cover;
  background-position:center;

}

.history-background{
  background: url("./champions.jpg")no-repeat;
  background-size:cover;
  height: 900px;
}

.fixture{
  background:url("./fixture.png")no-repeat;
  background-size:cover;
  height:600px;
  width: 1500px;
}

.logo{
  background:url("./mancity.png");
  -webkit-animation-name: "Animated_Logo";
          animation-name: "Animated_Logo";
  -webkit-animation-iteration-count:infinite;
          animation-iteration-count:infinite;
  -webkit-animation-duration: 5s;
          animation-duration: 5s;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  position: absolute;
  width:125px;
  height: 125px;
  left:10px;
}

button{
    background: skyblue;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 20px;
    margin: 0px 9px;
}



.app-header {
  background-color:lightblue;
  min-height: 100px;
  padding: 20px;
  min-width:100%;
  color: white;
  display:-ms-flexbox;
  display:flex;
  -ms-flex-pack:center;
      justify-content:center;
  position:fixed;

  
}

.app-intro {
  font-size: large;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }

}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }

}

@-webkit-keyframes Animated_Logo{
  0%{
    background-size: cover;
  }
  50%{
    width:80px;
    height:80px;
    background-size: cover;
  }
  100%{
    -webkit-transform:rotateY(360deg);
            transform:rotateY(360deg);
    background-size: cover;
  }

}

@keyframes Animated_Logo{
  0%{
    background-size: cover;
  }
  50%{
    width:80px;
    height:80px;
    background-size: cover;
  }
  100%{
    -webkit-transform:rotateY(360deg);
            transform:rotateY(360deg);
    background-size: cover;
  }

}





